.HotelCard{	
	border-radius: 8px;
	box-shadow: 16.9px 1.8px 23px 0 rgba(0, 0, 0, 0.22);
	background-color: #ddd;

	.main-content{
		display: flex;	
		border-bottom: 1px solid #666;
    border-radius: 8px;
    overflow: hidden;	
	}	
	.detail-content{		
		display: none;
		padding: 15px;		
		&.opened{
			display: block;
		}
	}
	.img-container{
		flex: 0 0 auto;
		width: 150px;
		height: 150px;
		@include breakpoint(xl){
			width: 200px;
			height: 200px;
		}
		img{
			display: block;
			width: 100%;
	    height: 100%;
	    object-fit: cover;
	    object-position: center center;
		}
	}
	.flex-container{
		flex: 1 1 auto;
		display: flex;
		flex-direction: column;
		@include breakpoint(sm){
			flex-direction: row;
		}
	}
	.shot-info-container{
		flex: 1 1 auto;
		padding: 5px;
		@include breakpoint(sm){
			padding: 30px;
		}
	}
	.hotel-name{
		font-size: 16px;
		@include breakpoint(xl){
			font-size: 24px;
		}
		font-weight: 700;
		text-transform: capitalize;
	}

	.action-container{
		padding: 0 5px 5px;
		display: flex;	  
	  align-items: flex-end;
		justify-content: flex-end;
		@include breakpoint(sm){
			padding: 15px;
			flex: 0 1 140px;
			justify-content: center;
		}
		@include breakpoint(xl){
			flex: 0 1 190px;
		}				
	}


	.rate-stars-container{	
		display: flex;
		.rate,.rateOk{
			width: 15px;
			height: 15px;
			@include breakpoint(xl){
				width: 20px;
				height: 20px;
			}				
		}
	}
	.description-container{
		margin-bottom: 20px;
		font-size: 18px;
    font-weight: 600;
    font-style: italic;
    line-height: 1.5;
	}
	.hasPool-container,.address-container{
		margin-bottom: 20px;
		font-size: 18px;
    font-weight: 600;
	}
	.price-container{
		margin-bottom: 20px;
		display: flex;
		flex-direction: column;
		@include breakpoint(xl){
			flex-direction: row;
		}
		font-size: 22px;
    font-weight: 700;
		.price{
			&:not(:last-child){
				margin-bottom: 10px;
			}
			@include breakpoint(xl){
				&:not(:last-child){
					margin-right: 20px;
				}
			}
		}
	}
	.location-container{
    display: flex;
    flex-wrap: wrap;		
		font-size: 18px;
    font-weight: 600;
    .location{
    	font-style: italic;
    	&:not(:last-child){
    		margin-right: 15px;
    	}
    }
	}
	
}
