.HotelList{	
	background-color: #cecece;
	padding: 20px 15px 50px;
	min-height: 100vh;	
	@include breakpoint(md){
		margin-left: $sidebar-width-md;
	}	
	@include breakpoint(xl){
		margin-left: $sidebar-width-xl;
		padding: 20px 30px 50px;
	}	
}
.hotelCard-container{
	&:not(:last-child){
		margin-bottom: 20px;
	}
}