.Checkbox{
	.label-checkbox{		
		display: flex;
		align-items: center;
		min-height: 30px;
		position: relative;		
		line-height: 1.28;
		color: #4a4a4a; 
		cursor: pointer; 
		font-family: $global-font-family;
		font-size: 16px;
		@include breakpoint(xl){
			font-size: 22px;
		}
		text-align: left;		
		.input{     
			//appearance: none;
			opacity: 0;
			position: absolute;
			height: 30px;
			width: 30px;
			top: 50%;
			transform: translateY(-50%);   
		}
		.custom-checkbox{
			margin: 0 15px 0 0;  
			height: 30px;
			width: 30px;  
			border: solid 1px #525252;   
			position: relative;
			background-color: transparent;
			flex-shrink: 0;
		} 
		.input:checked + .custom-checkbox{
			&:after{
				content: "";
				position: absolute; 
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
				background-color: #525252;
				width: 15px;
				height: 15px;
			}
		}
	}
}