.Filter{
	.filter-section{	
		&:not(:first-child){	
			padding: 5px 0 5px;
			@include breakpoint(md){	
				padding: 40px 0 20px;  
			}	
		}  
		&:first-child{
			padding: 0 0 5px;
			@include breakpoint(md){	
				padding: 0 0 20px;
			}
	  	border-bottom: 1px solid #666666;
		}
		&:not(:last-child){		
	  	border-bottom: 1px solid #666666;  	
		}	
		.section-title{
			margin-bottom: 5px;
			@include breakpoint(md){	
				margin-bottom: 20px;
			}
		}
	}
	.rating-container{
		display: flex;
    justify-content: center;
		margin-bottom: 5px;
		@include breakpoint(md){
			justify-content: flex-start;
			margin-bottom: 20px;
		}
	}

	.search-input-container{
		position: relative;
	}
	.search-input{	
		@include outline(0);
		background-color: transparent;
		border: 1px solid #525252;
		width: 100%;
		height: $search-input-height;
		padding: 5px 5px 5px $search-input-height;
	}
	.search-icon{
		width: calc( #{$search-input-height} * 0.6 );
		position: absolute;
		z-index: -1;
	  left: 0;
	  top: 50%;
	  transform: translate(20%, -50%);
	}
}