$font_default: "Open Sans", Helvetica, sans-serif;

$global-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; 
$secondary-font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;

$global-font-size: 16px;

// color
$color_default: rgba(0, 0, 0, 0.95);
$color_primary: #000000;
$text-color: $color_default;
$text-color-inverted: #ffffff;


//base of BS4 breakpoints
$screen-xs:					0;
$xs:						$screen-xs;
$screen-sm:					576px;
$sm:						$screen-sm;
$screen-md:					768px;
$md:						$screen-md;
$screen-lg:					992px;
$lg:						$screen-lg;
$screen-xl:					1200px;
$xl:						$screen-xl;
$screen-xxl:				1920px;
$xxl:						$screen-xxl;


//headings
$map-heading-size: (  
  h1: 40px,
  h2: 32px,
  h3: 24px,
  h4: 16px,
  h5: 13px, 
  h6: 10px, 
);
$scale-heading-for-xs:  1;
$scale-heading-for-sm:  1;
$scale-heading-for-md:  1;

$width-site: unset;
$sidebar-width-xl: 400px;
$sidebar-width-md: 250px;
$search-input-height: 50px;