.Rating{
	.rating-item-box,.rating-item-box-ok{
		position: relative;
		margin: 0 2px 0;
		@include breakpoint(xl){
			margin: 0 4px 0;
		}
	}	
	.rating-item-box-ok{		
		z-index: 1;
	}

	.rating-icon-text,.rating-icon-text-ok{
		position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 14px;
    @include breakpoint(xl){
    	font-size: 18px;
    }
    font-weight: 700;
	}
	.rating-icon-text-ok{		
    color: #ffffff;
	}
	.rating-icon{
		width: 40px;
		@include breakpoint(xl){
			width: 60px;
		}
	}
}