.detail-button{
	display: inline-block;
  min-width: 100px;
  height: 40px; 
  font-size: 14px;  
  
  @include breakpoint(xl){
  	min-width: 150px;
		height: 60px;
		font-size: 18px;
  }
	
	background: linear-gradient(333deg, #ec008c, #ff5555);
	border: none;
	border-radius: 5px;
	outline: 0;
	cursor: pointer;

	
	font-weight: 700;
	text-transform: capitalize;
	transition: all ease 0.5s;
	&:hover{		
		background: linear-gradient(333deg, #ff3322, #aa0000);
	}
}