html {
  box-sizing: border-box;  
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  //margin: 0;
  font-size: $global-font-size;
  font-family: $global-font-family;  
  color: $text-color;
  background-color: #fff;
}

a {
  cursor: pointer;
  color: $color_primary;
  &:hover,
  &:focus {
    color: lighten($color_primary, 10%);
  }
}

label {
  cursor: pointer;
}

img {
  max-width: 100%;
  max-height: 100%;
  user-select: none;
  -webkit-user-drag: none;
}

input[type='text'],
input[type='email'],
input[type='tel'],
input[type='password'],
input[type='search'],
textarea {
  &::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: lighten(gray, 20%);
  }
  &::-moz-placeholder {
    /* Firefox 19+ */
    color: lighten(gray, 20%);
  }
  &:-ms-input-placeholder {
    /* IE 10+ */
    color: lighten(gray, 20%);
  }
  &:-moz-placeholder {
    /* Firefox 18- */
    color: lighten(gray, 20%);
  }
}

textarea {
  resize: vertical;
}